d-color: #f0f2f5;\n    border-color: #117a8b;\n}\n#expand {\n    position: fixed;\n    top: 140px;\n    right: 170px;\n}\n.back-button {\n    text-align: right;\n}\n#map_container {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    position: inherit;\n    top: 90px;\n    right: 12px;\n    margin-top: 20px;\n}\n#map_container button {\n    position: absolute;\n    right: 20px;\n    top: 20px;\n    z-index: 500;\n}\n#mapid {\n    border-radius: 15px;\n    border-width: 5px;\n    border-color: black;\n    padding: 5px;\n    height: 600px;\n    width: 1300px;\n}\n.loader {\n    position: absolute;\n    display: flex;\n    justify-content: space-evenly;\n    align-items: center;\n    text-align: center;\n    top: 0px;\n    width: 100%;\n    height: 100%;\n    z-index: 401;\n    font-size: 32px;\n}\n</style>\n\n<script>\n    import { mapState } from 'vuex'\n    import { mapActions } from 'vuex'\n\n    export default {\n        props: [\n            'url_openstreetmap',\n        ],\n        data() {\n            return {\n                isHidden: true,\n            }\n        },\n        computed: {\n            ...mapState(['map']),\n        },\n        methods: {\n            ...mapActions(['requestMap']),\n            renderMap: function() {\n                const points = {\n                    'DEFAULT_CENTER': [-8.8315139, 125.6199236,9],\n                    'DEFAULT_ZOOM': 9,\n                }\n                this.timormap = L.map('mapid').setView(points.DEFAULT_CENTER, points.DEFAULT_ZOOM);\n                L.tileLayer(this.url_openstreetmap, {minZoom: 8, maxZoom: 18}).addTo(this.timormap);\n            },\n            renderGeoJSON: function(geojson) {\n                L.geoJSON(geojson, {\n                    style: function (feature) {\n                        return {color: 'orange'};\n                    }\n                }).bindPopup(function (layer) {\n                    var name = layer.feature.properties.name;\n                        return 'Distritu: ' + name[0] + name.substr(1).toLowerCase();\n                }).addTo(this.timormap);\n            },\n        },\n        watch: {\n            \"map.list\": function() {\n                this.renderGeoJSON(this.map.list);\n            }\n        },\n        mounted() {\n            this.renderMap();\n            if (this.map.list) {\n                this.renderGeoJSON(this.map.list);\n            } else {\n                this.requestMap();\n            }\n        },\n    }\n</script>\n"],"sourceRoot":""}]);
